import React from 'react';
import PropTypes from 'prop-types';
import {EditableSelect} from 'react-editable-select';
import { Panel, ListItem, Button, Group, Div, PanelHeader, FormLayout, FormLayoutGroup, Input, InfoRow, Radio, Select } from '@vkontakte/vkui';
import costs from './costs'

class Home extends React.Component {  
    
    constructor(props) {  
        super(props);  
        var curdate = new Date;
        this.state = {  
        	id: "home",
            result: "",
            currentTime: curdate.getHours() + ":" + curdate.getMinutes()
        };  
        this.updateMessage = this.updateMessage.bind(this);  
        this.onChangeTime = this.onChangeTime.bind(this); 
    }
   	updateMessage = (e) => {

		this.setState(state => ({
	      result: this.state.currentTime
	    }));
	}
	onChangeTime = (e) => {
		e.preventDefault();
		this.setState({ currentTime: e.target.value });
	}
    render() {  
         return (  
           <Panel id={this.state.id}>
	         <FormLayout>
		      <FormLayoutGroup>
		       <EditableSelect top="Номер маршрутки/автобуса/троллейбуса" placeholder="Выберите номер">
		        <option value="66">66</option>
				<option value="70">70</option>
				<option value="82с">82с</option>
				<option value="1т">1т</option>
				<option value="2а">2а</option>
				<option value="5к">5к</option>
				<option value="8">8</option>
				<option value="9">9</option>
				<option value="21">21</option>
				<option value="41">41</option>
				<option value="59">59</option>
				<option value="68">68</option>
				<option value="6">6</option>
				<option value="9к">9к</option>
				<option value="9м">9м</option>
				<option value="10">10</option>
				<option value="10а">10а</option>
				<option value="16">16</option>
				<option value="17">17</option>
				<option value="20">20</option>
				<option value="27">27</option>
				<option value="29">29</option>
				<option value="39">39</option>
				<option value="43">43</option>
				<option value="49">49</option>
				<option value="55">55</option>
				<option value="63">63</option>
				<option value="71">71</option>
				<option value="73">73</option>
				<option value="75">75</option>
				<option value="77">77</option>
				<option value="80">80</option>
				<option value="85">85</option>
				<option value="93">93</option>
				<option value="54">54</option>
				<option value="4">4</option>
				<option value="5">5</option>
				<option value="13">13</option>
				<option value="18">18</option>
				<option value="30">30</option>
				<option value="31">31</option>
				<option value="33">33</option>
				<option value="34">34</option>
				<option value="81">81</option>
				<option value="89">89</option>
		      </EditableSelect>
		       </FormLayoutGroup>
		       <FormLayoutGroup top="Время">
		        <Input type="text" defaultValue={this.state.currentTime} alignment="center" onChange={this.onChangeTime} />
		      </FormLayoutGroup>
		      <FormLayoutGroup top="Тип оплаты" >
	              <Radio name="type" value="1" defaultChecked>Наличные</Radio>
	              <Radio name="type" value="2">Карта</Radio>
	          </FormLayoutGroup>
	          <Button size="xl" level="primary" stretched="true" onClick={this.updateMessage}>Сколько стоит проезд?</Button>
		      <Div>
			      <InfoRow>
			      	{this.state.result}
			      </InfoRow>
		      </Div>
		    </FormLayout>
           </Panel> 
        )  
    }  
}

 export default Home;